
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  position: relative;
  overflow-x: hidden;
}

body {
  &.lock {
    overflow: hidden;
  }
}

.main {
  flex: 1 1 auto;
}

.footer {
  flex: 0 0;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
}

button {
  border: none;
  padding: 0;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
}

input {
  border: none;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
}

.global-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 15;
  width: 100%;
  height: 200%;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.3s linear, opacity 0.2s linear;

  @include mw2 {
    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: $fsnrml;
  line-height: 2;
  color: $firstcolor;
  background-color: $seccolor;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1290px;
}

.flex {
  display: flex;
}

.list-row {
  margin: -10px;

  li {
    padding: 10px;
  }
}

.section {
  padding: 90px 0;
}

.scroller {
  @include mw2 {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}

.main-title {
  font-weight: 500;
  font-size: $fsbig;
  line-height: 1.38;

  span {
    display: block;
    font-weight: 700;
  }

  @include cont {
    font-size: $fsbigcalc;
  }
}

.tiny-text {
  font-size: $fstiny;
}

.small-text {
  font-size: $fssmall;
}

.main-button {
  border-radius: 5rem;
  font-weight: 700;
  font-size: $fssmall;
  line-height: 1.25;
  background-color: $thirdcol;

  &--midl {
    padding: 1.65rem 6.03rem;
  }

  @include hover {
    &:hover {
      background-color: $thirdcolor1;
    }
  }
}

// margin

.marglrg {
  margin-bottom: 4.6rem;

  @include cont {
    margin-bottom: calc(1.5rem + 31 * ((100vw - 320px) / (1920 - 320)));
  }
}
