.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 50;
  width: 100%;
  min-height: 16.2rem;

  &__container {
    align-items: center;
    padding-top: 5rem;
    padding-bottom: 5rem;
    text-align: center;
    color: $seccolor;
  }

  &__logo {
    display: block;
    flex: 0 0 13rem;
    margin-right: 10px;
  }

  &__nav {
    flex: 1 1 auto;
    margin-right: 10px;
  }

  &__contacts {
    min-width: 19rem;
    max-width: 24rem;
  }
}

