.nav {
  &__list {
    justify-content: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 38px;
    }
  }

  &__link {
    display: block;
    height: 100%;
    font-size: $fssmall;
    line-height: 1.2;
    transition: color 0.2s linear;

    @include hover {
      &:hover {
        color: $thirdcol;
      }
    }
  }
}