.contacts {
  text-align: right;

  &__tel {
    display: block;
    font-weight: 600;
    font-size: $fslarge;
    line-height: 1.2;
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  &__text {
    display: block;
    margin-right: 0.7rem;
    padding-bottom: 0.8rem;
    font-size: $fsmicro;
    pointer-events: none;
  }
}