html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  position: relative;
  overflow-x: hidden;
}

body.lock {
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

.footer {
  flex: 0 0;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
}

button {
  border: none;
  padding: 0;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
}

input {
  border: none;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
}

.global-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 15;
  width: 100%;
  height: 200%;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.3s linear, opacity 0.2s linear;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 1.6rem;
  line-height: 2;
  color: #000;
  background-color: #fff;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1290px;
}

.flex {
  display: flex;
}

.list-row {
  margin: -10px;
}

.list-row li {
  padding: 10px;
}

.section {
  padding: 90px 0;
}

.main-title {
  font-weight: 500;
  font-size: 3.5rem;
  line-height: 1.38;
}

.main-title span {
  display: block;
  font-weight: 700;
}

.tiny-text {
  font-size: 1.3rem;
}

.small-text {
  font-size: 1.4rem;
}

.main-button {
  border-radius: 5rem;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.25;
  background-color: #bd9457;
}

.main-button--midl {
  padding: 1.65rem 6.03rem;
}

.marglrg {
  margin-bottom: 4.6rem;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 50;
  width: 100%;
  min-height: 16.2rem;
}

.header__container {
  align-items: center;
  padding-top: 5rem;
  padding-bottom: 5rem;
  text-align: center;
  color: #fff;
}

.header__logo {
  display: block;
  flex: 0 0 13rem;
  margin-right: 10px;
}

.header__nav {
  flex: 1 1 auto;
  margin-right: 10px;
}

.header__contacts {
  min-width: 19rem;
  max-width: 24rem;
}

.nav__list {
  justify-content: center;
}

.nav__item:not(:last-child) {
  margin-right: 38px;
}

.nav__link {
  display: block;
  height: 100%;
  font-size: 1.4rem;
  line-height: 1.2;
  transition: color 0.2s linear;
}

.contacts {
  text-align: right;
}

.contacts__tel {
  display: block;
  font-weight: 600;
  font-size: 2rem;
  line-height: 1.2;
}

.contacts__box {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.contacts__text {
  display: block;
  margin-right: 0.7rem;
  padding-bottom: 0.8rem;
  font-size: 1.1rem;
  pointer-events: none;
}

.social__link {
  padding: 0 4px;
}

.social__link svg {
  width: 15px;
  height: 15px;
}

.hero {
  position: relative;
  color: #fff;
  background-image: url("../images/hero/main-bg.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #f9f9f9;
}

.hero::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.49);
}

.hero__container {
  z-index: 2;
  padding-top: 26.4rem;
  padding-bottom: 4.5rem;
}

.hero__suptitle {
  max-width: 250px;
  font-size: 1.5rem;
  line-height: 1.53;
}

.hero__title {
  display: block;
  margin-bottom: 4rem;
  max-width: 68rem;
}

.hero__list {
  margin-bottom: 8rem;
}

.benefits-mini {
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 780px;
  pointer-events: none;
}

.benefits-mini__item {
  align-items: center;
}

.benefits-mini__icon {
  margin-right: 1rem;
  width: 6rem;
  height: 5.9rem;
}

.benefits-mini__icon svg {
  width: 100%;
  height: 100%;
}

.benefits-mini__text {
  display: inline-block;
  max-width: 180px;
  line-height: 1.77;
}

@media (min-width: 768px) {
  .main-button:hover {
    background-color: #bfaa86;
  }

  .nav__link:hover {
    color: #bd9457;
  }
}

@media (max-width: 1920px) {
  .main-title {
    font-size: calc(2.2rem + 7 * ((100vw - 320px) / (1920 - 320)));
  }

  .marglrg {
    margin-bottom: calc(1.5rem + 31 * ((100vw - 320px) / (1920 - 320)));
  }

  .hero {
    min-height: 100vh;
  }

  .hero__list {
    margin-bottom: calc(3rem + 50 * ((100vw - 320px) / (1920 - 320)));
  }
}

@media (max-width: 1920px) and (max-width: 1920px) {
  .hero__container {
    padding-top: calc(12rem + 146 * ((100vw - 320px) / (1920 - 320)));
  }

  .hero__title {
    margin-bottom: calc(1.7rem + 23 * ((100vw - 320px) / (1920 - 320)));
  }
}

@media (max-width: 1024px) {
  .overlay.active {
    opacity: 1;
    visibility: visible;
  }

  .scroller {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}