.hero {
  position: relative;
  color: $seccolor;
  background-image: url('../images/hero/main-bg.jpg');
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: $fourthcolor;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    background-color: rgba(#000, 0.49);
  }

  &__container {
    z-index: 2;
    padding-top: 26.4rem;
    padding-bottom: 4.5rem;
  }

  &__suptitle {
    max-width: 250px;
    font-size: $fsSeminrml;
    line-height: 1.53;
  }

  &__title {
    display: block;
    margin-bottom: 4rem;
    max-width: 68rem;
  }

  &__list {
    margin-bottom: 8rem;
  }

  &__link {
  }

  @include cont {
    min-height: 100vh;

    &__container {
      @include cont {
        padding-top: calc(12rem + 146 * ((100vw - 320px) / (1920 - 320)));
      }
    }

    &__title {
      @include cont {
        margin-bottom: calc(1.7rem + 23 * ((100vw - 320px) / (1920 - 320)));
      }
    }

    &__list {
      margin-bottom: calc(3rem + 50 * ((100vw - 320px) / (1920 - 320)));
    }
  }
}

.benefits-mini {
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 780px;
  pointer-events: none;

  &__item {
    align-items: center;
  }

  &__icon {
    margin-right: 1rem;
    width: 6rem;
    height: 5.9rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    display: inline-block;
    max-width: 180px;
    line-height: 1.77;
  }
}
